
import { defineComponent, ref } from 'vue'

import { useRouter } from "vue-router"

import axios from 'axios'

import {Toast} from 'vant'

export default defineComponent({
  setup () {

    let router: any = useRouter();
    
    let show: any = ref<boolean>(false);

    let loginUserInfo: any = ref<any>({
      phone: '',
      password: ''
    })

    let registerUserInfo: any = ref<any>({
      phone: '',
      password: '',
      nickName: ''
    })

    let isOpenRegister: any = ref<boolean>(false);

    let isOpenLogin: any = ref<boolean>(false);

    let showPopup:() => void = (): void => {
      show.value = !show.value;
      
    }

    //查看首页
    let goPage: (name: string) => void = (name: string): void => {
      router.push({name});
    }

    //切换注册密码可见状态
    let toggleResgiterPasssword: () => void = (): void => {
      isOpenRegister.value = !isOpenRegister.value;
    }

    //切换登录密码可见状态toggleLoginPasssword
    let toggleLoginPasssword: () => void = (): void => {
      isOpenLogin.value = !isOpenLogin.value;
    }

    //注册
    let register: () => void = (): void => {
      axios({
        method: 'post',
        url: '/register',
        data: {
          ...registerUserInfo.value
        }
      }).then((result: any): void => {
      
        Toast(result.data.msg);

        if (result.data.code === 100) {
           
            show.value = false;
           //清除用户注册信息
           for (let key in registerUserInfo.value) {
             registerUserInfo.value[key] = '';
           }
        }
      }).catch((err: any): void => {
        
      })
    }

    //登录
    let login: () => void = (): void => {
      axios({
        method: 'post',
        url: '/login',
        data: {
          ...loginUserInfo.value
        }
      }).then((result: any): void => {
        

        Toast(result.data.msg);

        if (result.data.code === 200) {

          //讲token保存到localStorage
          localStorage.setItem('token', result.data.token);

          setTimeout(() => {
            router.push({name: 'Home'});
          }, 1000)
           
        }
      }).catch((err: any): void => {
        
      })
    }

    return {
      show,
      loginUserInfo,
      registerUserInfo,
      isOpenRegister,
      isOpenLogin,
      showPopup,
      goPage,
      toggleResgiterPasssword,
      toggleLoginPasssword,
      register,
      login
    }
  }
})
